import React, { useState, useEffect } from "react";
import {
  format,
  isBefore,
  isAfter,
  isThisWeek,
  isToday,
  isTomorrow,
  addDays,
  startOfWeek,
  parseISO,
  differenceInCalendarDays,
  isWithinInterval,
  subDays,
  startOfDay,
  endOfDay,
  startOfMonth,
  endOfMonth,
  startOfYear,
  endOfYear,
} from "date-fns";
import { PieChart } from "react-minimal-pie-chart";

// Load tasks and history from localStorage
function getInitialTasks() {
  const data = localStorage.getItem("asan-hai-tasks");
  if (data) return JSON.parse(data);
  return [];
}
function getInitialHistory() {
  const data = localStorage.getItem("asan-hai-history");
  if (data) return JSON.parse(data);
  return [];
}
// Load categories from localStorage
function getInitialCategories() {
  const data = localStorage.getItem("asan-hai-categories");
  if (data) return JSON.parse(data);
  // Default example category
  return [
    { id: 1, name: "General", color: "#4056f4" },
  ];
}

const FILTERS = [
  { label: "All", value: "all" },
  { label: "Today", value: "today" },
  { label: "Tomorrow", value: "tomorrow" },
  { label: "Day After Tomorrow", value: "dayaftertomorrow" },
  { label: "Pending", value: "pending" },
  { label: "Upcoming", value: "upcoming" },
];

const REPORT_FILTERS = [
  { label: "Daily", value: "daily" },
  { label: "Weekly", value: "weekly" },
  { label: "Monthly", value: "monthly" },
  { label: "Custom Range", value: "custom" },
];

export default function App() {
  // Tasks, history, categories states
  const [tasks, setTasks] = useState(getInitialTasks());
  const [history, setHistory] = useState(getInitialHistory());
  const [categories, setCategories] = useState(getInitialCategories());

  // New task inputs (including categories)
  const [newTask, setNewTask] = useState({
    title: "",
    desc: "",
    deadline: "",
    categoryIds: [],
  });

  // Bulk input & UI states
  const [bulkInput, setBulkInput] = useState("");
  const [showBulk, setShowBulk] = useState(false);
  const [showEditTaskId, setShowEditTaskId] = useState(null);
  const [editTaskData, setEditTaskData] = useState(null);

  // Category creation UI
  const [showCategoryCreator, setShowCategoryCreator] = useState(false);
  const [newCategoryName, setNewCategoryName] = useState("");
  const [newCategoryColor, setNewCategoryColor] = useState("#4056f4");

  // Report UI states
  const [showReport, setShowReport] = useState(false);
  const [reportFilter, setReportFilter] = useState("weekly");
  const [customReportRange, setCustomReportRange] = useState({ from: "", to: "" });
  const [reportCategoryClicked, setReportCategoryClicked] = useState(null);

  // Filters & calendar navigation
  const [filter, setFilter] = useState("all");
  const [weekOffset, setWeekOffset] = useState(0);

  // Save to localStorage on changes
  useEffect(() => {
    localStorage.setItem("asan-hai-tasks", JSON.stringify(tasks));
    localStorage.setItem("asan-hai-history", JSON.stringify(history));
    localStorage.setItem("asan-hai-categories", JSON.stringify(categories));
    document.title = "ASAN HAI";
  }, [tasks, history, categories]);

  // --- Task handlers ---
  const handleBulkAdd = () => {
    const newTasks = [];
    bulkInput.split("\n").forEach((line) => {
      const [title, desc = "", deadline = "", categoryName = ""] = line.split(",").map((f) => f.trim());
      if (title && deadline) {
        // Find category IDs by name (or fallback to default category)
        let categoryIds = [];
        if (categoryName) {
          const foundCat = categories.find(c => c.name.toLowerCase() === categoryName.toLowerCase());
          if (foundCat) categoryIds.push(foundCat.id);
          else categoryIds.push(categories[0].id); // default category id
        } else {
          categoryIds.push(categories[0].id);
        }
        newTasks.push({
          id: Date.now() + Math.random(),
          title,
          desc,
          deadline,
          categoryIds,
          completed: false,
          created: new Date().toISOString(),
        });
      }
    });
    if (newTasks.length > 0) {
      setTasks([...tasks, ...newTasks]);
      setBulkInput("");
      setShowBulk(false);
    }
  };

  const handleAddTask = () => {
    if (!newTask.title || !newTask.deadline) return;
    setTasks([
      ...tasks,
      {
        id: Date.now(),
        title: newTask.title,
        desc: newTask.desc,
        deadline: newTask.deadline,
        categoryIds: newTask.categoryIds,
        completed: false,
        created: new Date().toISOString(),
      },
    ]);
    setNewTask({ title: "", desc: "", deadline: "", categoryIds: [] });
  };

  const openEditTask = (task) => {
    setShowEditTaskId(task.id);
    setEditTaskData({ ...task }); // clone to edit
  };

  const handleEditTaskSave = () => {
    if (!editTaskData.title || !editTaskData.deadline) return;
    setTasks(tasks.map(t => t.id === showEditTaskId ? { ...editTaskData } : t));
    setShowEditTaskId(null);
    setEditTaskData(null);
  };

  const handleDeleteTask = (id) => {
    setTasks(tasks.filter((t) => t.id !== id));
  };

  const handleToggleComplete = (id) => {
    setTasks(
      tasks.map((t) =>
        t.id === id ? { ...t, completed: !t.completed } : t
      )
    );
    const t = tasks.find((t) => t.id === id);
    setHistory([
      ...history,
      { type: "completed", title: t.title, date: new Date().toISOString() }
    ]);
  };

  // --- Category handlers ---
  const handleAddCategory = () => {
    if (!newCategoryName) return;
    // Create new category with unique ID
    const newCat = {
      id: Date.now(),
      name: newCategoryName,
      color: newCategoryColor,
    };
    setCategories([...categories, newCat]);
    setNewCategoryName("");
    setNewCategoryColor("#4056f4");
    setShowCategoryCreator(false);
  };

  // --- Filters ---
  const now = new Date();
  const weekStartDate = addDays(startOfWeek(now, { weekStartsOn: 1 }), weekOffset * 7);
  const calendarDays = Array.from({ length: 7 }).map((_, i) => addDays(weekStartDate, i));

  const filteredTasks = tasks.filter((t) => {
    const d = parseISO(t.deadline);
    if (filter === "all") return true;
    if (filter === "today") return isToday(d);
    if (filter === "tomorrow") return isTomorrow(d);
    if (filter === "dayaftertomorrow") return differenceInCalendarDays(d, now) === 2;
    if (filter === "pending") return !t.completed && (isBefore(d, now) || isToday(d));
    if (filter === "upcoming") return isAfter(d, now);
    return true;
  });

  const pending = tasks.filter(
    (t) => !t.completed && (isBefore(parseISO(t.deadline), now) || isToday(parseISO(t.deadline)))
  );
  const overdue = tasks.filter(
    (t) => !t.completed && isBefore(parseISO(t.deadline), now)
  );

  // --- Reports ---

  // Filter tasks by date range for report
  const filterTasksByDateRange = (tasks, fromDate, toDate) => {
    if (!fromDate || !toDate) return [];
    const from = startOfDay(parseISO(fromDate));
    const to = endOfDay(parseISO(toDate));
    return tasks.filter(t => {
      const d = parseISO(t.deadline);
      return isWithinInterval(d, { start: from, end: to });
    });
  };

  // Get report date range by selected filter
  const getReportDateRange = () => {
    const todayStr = format(now, "yyyy-MM-dd");
    switch (reportFilter) {
      case "daily":
        return { from: todayStr, to: todayStr };
      case "weekly":
        return {
          from: format(startOfWeek(now, { weekStartsOn: 1 }), "yyyy-MM-dd"),
          to: format(addDays(startOfWeek(now, { weekStartsOn: 1 }), 6), "yyyy-MM-dd"),
        };
      case "monthly":
        return {
          from: format(startOfMonth(now), "yyyy-MM-dd"),
          to: format(endOfMonth(now), "yyyy-MM-dd"),
        };
      case "custom":
        return customReportRange;
      default:
        return { from: "", to: "" };
    }
  };

  const { from, to } = getReportDateRange();
  const reportTasks = filterTasksByDateRange(tasks, from, to);

  const completedTasks = reportTasks.filter(t => t.completed);
  const incompleteTasks = reportTasks.filter(t => !t.completed);

  // Compute pie chart data: group tasks by category
  const pieData = categories.map(cat => {
    const catTasks = reportTasks.filter(t => t.categoryIds.includes(cat.id));
    return {
      title: cat.name,
      value: catTasks.length,
      color: cat.color,
      tasks: catTasks,
    };
  }).filter(d => d.value > 0);

  const totalTasksCount = reportTasks.length;

  // Pie segment click handler
  const onPieSegmentClick = (segment) => {
    setReportCategoryClicked(segment.title === reportCategoryClicked ? null : segment.title);
  };

  // Get tasks of clicked category for report display
  const clickedCategoryData = pieData.find(p => p.title === reportCategoryClicked);

  return (
    <div style={{ maxWidth: 800, margin: "0 auto", padding: 24, fontFamily: "Helvetica, Arial, sans-serif" }}>
      <h1 style={{ textAlign: "center", fontSize: 32 }}>ASAN HAI</h1>

      {/* Add Task Section */}
      <div style={{
        margin: "28px 0 20px 0", padding: 20, borderRadius: 16,
        border: "2px solid #bfc9f6", background: "#f6f8ff", display: "flex", flexDirection: "column", gap: 16
      }}>
        <div style={{ fontWeight: 600, fontSize: 20, marginBottom: 8 }}>Add Task</div>
        <div style={{ display: "flex", gap: 12, alignItems: "center", flexWrap: "wrap" }}>
          <input
            placeholder="Task Title"
            value={newTask.title}
            onChange={e => setNewTask({ ...newTask, title: e.target.value })}
            style={{ flex: "1 1 200px", padding: 10, borderRadius: 7, fontSize: 16, border: "1px solid #ccd" }}
          />
          <input
            type="date"
            value={newTask.deadline}
            onChange={e => setNewTask({ ...newTask, deadline: e.target.value })}
            style={{ flex: "0 0 150px", padding: 10, borderRadius: 7, fontSize: 16, border: "1px solid #ccd" }}
          />
          <input
            placeholder="Description"
            value={newTask.desc}
            onChange={e => setNewTask({ ...newTask, desc: e.target.value })}
            style={{ flex: "2 1 250px", padding: 10, borderRadius: 7, fontSize: 16, border: "1px solid #ccd" }}
          />
          {/* Category multi-select */}
          <select
            multiple
            value={newTask.categoryIds}
            onChange={e => {
              const options = e.target.options;
              const selected = [];
              for (let i = 0; i < options.length; i++) {
                if (options[i].selected) selected.push(parseInt(options[i].value));
              }
              setNewTask({ ...newTask, categoryIds: selected });
            }}
            style={{ flex: "1 1 150px", padding: 10, borderRadius: 7, fontSize: 16, border: "1px solid #ccd", height: 40 }}
            title="Hold Ctrl (Cmd on Mac) to select multiple"
          >
            {categories.map(c => (
              <option key={c.id} value={c.id} style={{ color: c.color }}>{c.name}</option>
            ))}
          </select>
          <button onClick={handleAddTask} style={{
            padding: "14px 28px", borderRadius: 10, fontWeight: 600, fontSize: 16,
            background: "#4056f4", color: "#fff", border: "none", marginTop: 20
          }}>Add</button>
        </div>

        {/* Category create UI */}
        <button onClick={() => setShowCategoryCreator(true)} style={{
          marginTop: 10, padding: "8px 20px", borderRadius: 8, fontWeight: 500, fontSize: 15,
          background: "#fff", border: "1.5px solid #98b2fa", color: "#4056f4", cursor: "pointer"
        }}>Manage Categories</button>

        {showCategoryCreator && (
          <div style={{
            marginTop: 14, padding: 16, border: "1px solid #ccd", borderRadius: 10, background: "#f7f9ff"
          }}>
            <h3 style={{ marginBottom: 8 }}>Create New Category</h3>
            <input
              placeholder="Category Name"
              value={newCategoryName}
              onChange={e => setNewCategoryName(e.target.value)}
              style={{ padding: 8, borderRadius: 6, border: "1px solid #bbb", fontSize: 16, marginBottom: 8 }}
            />
            <input
              type="color"
              value={newCategoryColor}
              onChange={e => setNewCategoryColor(e.target.value)}
              style={{ width: 50, height: 32, border: "none", cursor: "pointer", marginBottom: 12 }}
            />
            <div>
              <button onClick={handleAddCategory} style={{
                padding: "8px 18px", marginRight: 10, background: "#4056f4", color: "#fff", border: "none", borderRadius: 7, fontWeight: 600,
                cursor: newCategoryName ? "pointer" : "not-allowed",
                opacity: newCategoryName ? 1 : 0.5
              }} disabled={!newCategoryName}>Add Category</button>
              <button onClick={() => setShowCategoryCreator(false)} style={{
                padding: "8px 18px", background: "#eee", color: "#222", border: "1px solid #aaa", borderRadius: 7, fontWeight: 500
              }}>Cancel</button>
            </div>
          </div>
        )}

      </div>

      {/* Bulk Add Section */}
      <div style={{ margin: "0 0 12px 0", textAlign: "right" }}>
        <button
          onClick={() => setShowBulk(true)}
          style={{
            padding: "9px 22px", borderRadius: 8, fontWeight: 500, fontSize: 15,
            background: "#fff", border: "1.5px solid #98b2fa", color: "#4056f4", marginRight: 6, cursor: "pointer"
          }}>
          Bulk Add Tasks
        </button>
      </div>

      {showBulk && (
        <div style={{
          position: "fixed", left: 0, top: 0, width: "100vw", height: "100vh",
          background: "rgba(0,0,0,0.14)", display: "flex", alignItems: "center", justifyContent: "center", zIndex: 1000
        }}>
          <div style={{ background: "#fff", padding: 28, borderRadius: 14, minWidth: 350, width: 410, maxWidth: "80vw", boxShadow: "0 8px 44px #8fa0f845" }}>
            <h3 style={{ marginBottom: 14, color: "#253272" }}>Bulk Add Tasks</h3>
            <textarea
              value={bulkInput}
              onChange={e => setBulkInput(e.target.value)}
              rows={8}
              placeholder='Paste tasks here:\nTitle, Description, YYYY-MM-DD, CategoryName (one task per line)'
              style={{ width: "100%", padding: 10, borderRadius: 7, border: "1px solid #ccd", marginBottom: 14, fontSize: 15 }}
            />
            <div>
              <button onClick={handleBulkAdd}
                style={{ padding: "10px 20px", borderRadius: 7, background: "#4056f4", color: "#fff", fontWeight: 600, border: "none", marginRight: 10 }}>
                Add Bulk Tasks
              </button>
              <button onClick={() => setShowBulk(false)}
                style={{ padding: "10px 20px", borderRadius: 7, background: "#f4f4f6", color: "#222", border: "1px solid #ccd" }}>
                Cancel
              </button>
              <div style={{ fontSize: 12, color: "#888", marginTop: 10 }}>
                Format: Title, Description, YYYY-MM-DD, CategoryName (one task per line)
              </div>
            </div>
          </div>
        </div>
      )}

      {/* WEEKLY CALENDAR NAVIGATION */}
      <h3 style={{ marginTop: 30, display: "flex", alignItems: "center", justifyContent: "center", gap: 14 }}>
        <button onClick={() => setWeekOffset(weekOffset - 1)} style={{
          fontSize: 20, border: "none", background: "#f0f0f8", borderRadius: "50%", width: 38, height: 38, cursor: "pointer", fontWeight: 700
        }}>&lt;</button>
        <span>
          {format(weekStartDate, "MMM dd")}  {format(addDays(weekStartDate, 6), "MMM dd, yyyy")}
        </span>
        <button onClick={() => setWeekOffset(weekOffset + 1)} style={{
          fontSize: 20, border: "none", background: "#f0f0f8", borderRadius: "50%", width: 38, height: 38, cursor: "pointer", fontWeight: 700
        }}>&gt;</button>
      </h3>
      <div style={{ display: "flex", gap: 8, marginBottom: 20 }}>
        {calendarDays.map(day => {
          const todaysTasks = tasks.filter(t => t.deadline === format(day, "yyyy-MM-dd"));
          return (
            <div key={day} style={{ flex: 1, minWidth: 90, background: "#fafafa", borderRadius: 8, border: "1px solid #eee", padding: 8 }}>
              <div style={{ fontWeight: "bold", textAlign: "center", fontSize: 13, marginBottom: 4 }}>
                {format(day, "EEE")}
                <br />
                <span style={{ fontSize: 11 }}>{format(day, "MM/dd")}</span>
              </div>
              <div style={{ color: todaysTasks.length === 0 ? "#bbb" : "#4056f4", textAlign: "center", fontSize: 16, fontWeight: 600, marginTop: 10 }}>
                {todaysTasks.length === 0 ? "No tasks" : `${todaysTasks.length} task${todaysTasks.length > 1 ? "s" : ""}`}
              </div>
            </div>
          );
        })}
      </div>

      {/* FILTER BAR */}
      <div style={{ display: "flex", gap: 12, margin: "18px 0", justifyContent: "center" }}>
        {FILTERS.map(f => (
          <button
            key={f.value}
            onClick={() => setFilter(f.value)}
            style={{
              padding: "10px 24px",
              fontWeight: filter === f.value ? 700 : 500,
              borderRadius: 8,
              border: "1px solid #ddd",
              background: filter === f.value ? "#e7e7fa" : "#fff",
              cursor: "pointer",
            }}
          >
            {f.label}
          </button>
        ))}
      </div>

      {/* MY TASKS */}
      <div>
        <h2>My Tasks</h2>
        {filteredTasks.map((t) => (
          <div key={t.id} style={{
            border: "1px solid #ddd", borderRadius: 8, margin: "12px 0",
            padding: 16, display: "flex", alignItems: "center", background: t.completed ? "#e8ffe8" : "#fff",
            boxShadow: "0 2px 8px #f1f1f1"
          }}>
            <div style={{ flex: 1 }}>
              <div style={{ display: "flex", alignItems: "center", gap: 10, flexWrap: "wrap" }}>
                <strong style={{ textDecoration: t.completed ? "line-through" : "", fontSize: 18 }}>
                  {t.title}
                </strong>
                <span style={{
                  color: "#4056f4", fontWeight: 600, fontSize: 15, border: "1px solid #cfd8fc", borderRadius: 6,
                  padding: "2px 8px", background: "#f2f6fd"
                }}>
                  {format(parseISO(t.deadline), "dd/MM/yyyy")}
                </span>
                {/* Show category tags */}
                {t.categoryIds.map(catId => {
                  const cat = categories.find(c => c.id === catId);
                  if (!cat) return null;
                  return (
                    <span key={cat.id} style={{
                      backgroundColor: cat.color,
                      color: "white",
                      padding: "3px 8px",
                      borderRadius: 6,
                      fontSize: 13,
                      fontWeight: 600,
                      userSelect: "none",
                      marginLeft: 6
                    }}>{cat.name}</span>
                  );
                })}
              </div>
              {t.desc &&
                <div style={{ color: "#777", fontSize: 15, marginTop: 7, marginLeft: 2, textAlign: "left" }}>
                  {t.desc}
                </div>
              }
            </div>
            <div style={{
              display: "flex", gap: 12, background: "#f6f8ff", borderRadius: 8,
              padding: "7px 13px", boxShadow: "0 1px 3px #f0f4fb"
            }}>
              <button onClick={() => handleToggleComplete(t.id)} style={{
                padding: "7px 16px", borderRadius: 7, border: "none",
                background: t.completed ? "#c8ebfa" : "#4056f4", color: t.completed ? "#4056f4" : "#fff", fontWeight: 600, cursor: "pointer"
              }}>
                {t.completed ? "Undo" : "Complete"}
              </button>
              <button onClick={() => openEditTask(t)} style={{
                padding: "7px 16px", borderRadius: 7, border: "none",
                background: "#ffeecd", color: "#7a5b12", fontWeight: 600, cursor: "pointer"
              }}>
                Edit Task
              </button>
              <button onClick={() => handleDeleteTask(t.id)} style={{
                padding: "7px 16px", borderRadius: 7, border: "none",
                background: "#ffeded", color: "#d02222", fontWeight: 600, cursor: "pointer"
              }}>
                Delete
              </button>
            </div>
          </div>
        ))}
      </div>

      {/* EDIT TASK MODAL */}
      {showEditTaskId && editTaskData && (
        <div style={{
          position: "fixed", left: 0, top: 0, width: "100vw", height: "100vh",
          background: "rgba(0,0,0,0.14)", display: "flex", alignItems: "center", justifyContent: "center", zIndex: 1002
        }}>
          <div style={{ background: "#fff", padding: 24, borderRadius: 10, boxShadow: "0 4px 32px #d5c98799", minWidth: 280 }}>
            <h3>Edit Task</h3>
            <input
              placeholder="Task Title"
              value={editTaskData.title}
              onChange={e => setEditTaskData({ ...editTaskData, title: e.target.value })}
              style={{ padding: 10, marginBottom: 10, borderRadius: 7, border: "1px solid #ccd", fontSize: 16, width: "100%" }}
            />
            <input
              type="date"
              value={editTaskData.deadline}
              onChange={e => setEditTaskData({ ...editTaskData, deadline: e.target.value })}
              style={{ padding: 10, marginBottom: 10, borderRadius: 7, border: "1px solid #ccd", fontSize: 16, width: "100%" }}
            />
            <input
              placeholder="Description"
              value={editTaskData.desc}
              onChange={e => setEditTaskData({ ...editTaskData, desc: e.target.value })}
              style={{ padding: 10, marginBottom: 10, borderRadius: 7, border: "1px solid #ccd", fontSize: 16, width: "100%" }}
            />
            {/* Category multi-select */}
            <select
              multiple
              value={editTaskData.categoryIds}
              onChange={e => {
                const options = e.target.options;
                const selected = [];
                for (let i = 0; i < options.length; i++) {
                  if (options[i].selected) selected.push(parseInt(options[i].value));
                }
                setEditTaskData({ ...editTaskData, categoryIds: selected });
              }}
              style={{ padding: 10, borderRadius: 7, border: "1px solid #ccd", fontSize: 16, width: "100%", height: 80 }}
              title="Hold Ctrl (Cmd on Mac) to select multiple"
            >
              {categories.map(c => (
                <option key={c.id} value={c.id} style={{ color: c.color }}>{c.name}</option>
              ))}
            </select>

            <div style={{ marginTop: 12 }}>
              <button onClick={handleEditTaskSave} style={{
                padding: "8px 18px", marginRight: 10, background: "#4056f4", color: "#fff", border: "none", borderRadius: 7, fontWeight: 600
              }}>Save</button>
              <button onClick={() => { setShowEditTaskId(null); setEditTaskData(null); }} style={{
                padding: "8px 18px", background: "#eee", color: "#222", border: "1px solid #aaa", borderRadius: 7, fontWeight: 500
              }}>Cancel</button>
            </div>
          </div>
        </div>
      )}

      {/* REPORT BUTTON */}
      <div style={{ marginTop: 32 }}>
        <button onClick={() => setShowReport(true)}
          style={{
            padding: "9px 22px", borderRadius: 8, fontWeight: 500, fontSize: 15,
            background: "#fff", border: "1.5px solid #98b2fa", color: "#4056f4", marginRight: 6, cursor: "pointer"
          }}
        >Show Report</button>
      </div>

      {/* REPORT MODAL */}
      {showReport && (
        <div style={{
          position: "fixed", left: 0, top: 0, width: "100vw", height: "100vh",
          background: "rgba(0,0,0,0.08)", display: "flex", alignItems: "center", justifyContent: "center", zIndex: 1003
        }}>
          <div style={{ background: "#fff", padding: 32, borderRadius: 16, minWidth: 320, maxWidth: 600, maxHeight: "90vh", overflowY: "auto" }}>
            <h2>Report</h2>

            {/* Report filter */}
            <div style={{ marginBottom: 18 }}>
              {REPORT_FILTERS.map(f => (
                <button
                  key={f.value}
                  onClick={() => { setReportFilter(f.value); setReportCategoryClicked(null); }}
                  style={{
                    padding: "8px 18px",
                    marginRight: 8,
                    borderRadius: 8,
                    border: reportFilter === f.value ? "2px solid #4056f4" : "1px solid #ddd",
                    background: reportFilter === f.value ? "#e7e7fa" : "#fff",
                    cursor: "pointer",
                  }}
                >
                  {f.label}
                </button>
              ))}
            </div>

            {/* Custom date range picker */}
            {reportFilter === "custom" && (
              <div style={{ marginBottom: 20 }}>
                <label>
                  From:{" "}
                  <input
                    type="date"
                    value={customReportRange.from}
                    onChange={e => setCustomReportRange({ ...customReportRange, from: e.target.value })}
                    style={{ marginRight: 12, padding: 6, borderRadius: 6, border: "1px solid #ccc" }}
                  />
                </label>
                <label>
                  To:{" "}
                  <input
                    type="date"
                    value={customReportRange.to}
                    onChange={e => setCustomReportRange({ ...customReportRange, to: e.target.value })}
                    style={{ padding: 6, borderRadius: 6, border: "1px solid #ccc" }}
                  />
                </label>
              </div>
            )}

            <div><strong>Total tasks:</strong> {totalTasksCount}</div>
            <div><strong>Completed tasks:</strong> {completedTasks.length}</div>
            <div><strong>Incomplete tasks:</strong> {incompleteTasks.length}</div>

            {/* Pie Chart */}
            <div style={{ margin: "20px 0" }}>
              {pieData.length === 0 ? (
                <div style={{ color: "#888", fontStyle: "italic" }}>No tasks in selected range.</div>
              ) : (
                <PieChart
                  data={pieData}
                  animate
                  label={({ dataEntry }) => dataEntry.value}
                  labelStyle={{ fontSize: "6px", fill: "white" }}
                  labelPosition={70}
                  radius={40}
                  segmentsStyle={{ cursor: "pointer" }}
                  onClick={(_, index) => onPieSegmentClick(pieData[index])}
                />
              )}
            </div>

            {/* Show tasks of clicked category */}
            {clickedCategoryData && (
              <div>
                <h3 style={{ marginTop: 0, marginBottom: 6, color: clickedCategoryData.color }}>
                  {clickedCategoryData.title} Tasks ({clickedCategoryData.value})
                </h3>
                <ul>
                  {clickedCategoryData.tasks.map(t => (
                    <li key={t.id}>{t.title} ({format(parseISO(t.deadline), "dd/MM/yyyy")})</li>
                  ))}
                </ul>
              </div>
            )}

            <div style={{ marginTop: 20, textAlign: "right" }}>
              <button onClick={() => setShowReport(false)} style={{
                padding: "8px 22px", background: "#4056f4", color: "#fff", border: "none", borderRadius: 8, fontWeight: 600
              }}>Close</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
